import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';
import { Venta } from '../interfaces/venta.model';

import { NgForm } from '@angular/forms';
import { SaleService } from '../../../../shared/services/sale.service';
import { Sale } from '../../../../shared/models/sale';
import { StorageService } from 'src/app/shared/services/storage/storage.service';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'vex-venta-create-update',
  templateUrl: './venta-create-update.component.html',
  styleUrls: ['./venta-create-update.component.scss']
})
export class VentaCreateUpdateComponent implements OnInit {

  ventasWeb: Sale = new Sale();
  
  loading = false;
  saleList: Sale[];
  saleObject: Sale;

  static id = 100;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;

  constructor(
    @Inject(MAT_DIALOG_DATA) 
    public defaults: any,
    private dialogRef: MatDialogRef<VentaCreateUpdateComponent>,
    private fb: FormBuilder,
    private salesService: SaleService,
    private firestore: AngularFirestore,
    private storageService: StorageService
    ) {}

  ngOnInit() {
    if(this.storageService.getSession("$key")!=null){
      this.ventasWeb = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as Sale;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({

      $key: newId,
      id: [VentaCreateUpdateComponent.id++],
      imagen: this.defaults.imagen || '',
      total: this.defaults.total,
      costo: [this.defaults.costo || ''],
      cantidad: [this.defaults.cantidad || ''],
      placa: this.defaults.placa || '',
      ubicacion: this.defaults.ubicacion || '',
      //urlImage: this.defaults.urlImage || '',
      formaPago: this.defaults.formaPago || '',
      producto: this.defaults.producto || '',

      /*producto: string;
      cantidad: number;
      costo: string;
      placa: string;
      ubicacion: string;
      formaPago: string;
      total:string;
      imagen: string;*/
    });
  }



save(saleForm: NgForm) {
  if (this.mode === 'create') {
    this.addSale(saleForm);
  } else if (this.mode === 'update') {
    this.updateSale();
  }
}

addSale(saleForm: NgForm) {
  this.salesService.postSale(saleForm.value);
  this.createVenta();
  alert("Venta guardada")

}

updateSale(){
  if(this.storageService.getSession("$key")!=null){
    //var value = new AddEstablishment
      var values = { 
        id:  this.ventasWeb.id,
        producto: this.ventasWeb.producto,    
        cantidad: this.ventasWeb.cantidad,
        costo: this.ventasWeb.costo,
        placa: this.ventasWeb.placa,
        ubicacion: this.ventasWeb.ubicacion,
        formaPago: this.ventasWeb.formaPago,
        total: this.ventasWeb.total,
        imagen: this.ventasWeb.imagen,
      }

      const venta = this.form.value;  
      venta.producto= String(values.producto)
      venta.cantidad= Number(values.cantidad)
      venta.costo = String(values.costo)
      venta.placa = String(values.placa)
      venta.ubicacion = String(values.ubicacion)
      venta.formaPago = Number(values.formaPago)
      venta.total = String(values.total)
      venta.imagen = String(values.imagen)
      this.salesService.updateSale(values)
      this.updateVenta()
      console.log('print value::'+ JSON.stringify(venta));
    }else{
      console.log('no entro')
    }
}


createVenta() {
  const venta = this.form.value;

  if (!venta.imageSrc) {
    venta.imageSrc = 'assets/img/avatars/1.jpg';
  }

  this.dialogRef.close(venta);
}

updateVenta() {
  const venta = this.form.value;
  venta.id = this.defaults.id;

  this.dialogRef.close(venta);
}

isCreateMode() {
  return this.mode === 'create';
}

isUpdateMode() {
  return this.mode === 'update';
}
}
